@font-face {
  font-family: AdidasFont;
  src: url("../AdihausDIN.073413d5.ttf");
}

* {
  box-sizing: border-box;
  letter-spacing: .01rem;
  margin: 0;
  padding: 0;
  font-family: AdidasFont, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body, main {
  width: 100%;
}

header {
  border-bottom: 2px solid #000;
  width: 100%;
  height: auto;
  padding: 5rem 0;
  position: relative;
}

@media (width <= 48rem) {
  header {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0;
    display: flex;
  }
}

header .header-container {
  background-color: #0000;
  justify-content: space-between;
  align-items: center;
  width: min(90%, 90rem);
  margin-inline: auto;
  padding-right: 10rem;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container {
    text-align: center;
    flex-direction: column;
    margin-bottom: 1.5rem;
    padding-right: 0;
  }
}

header .header-container .name-conference-division {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container .name-conference-division {
    align-items: center;
  }
}

header .header-container .name-conference-division h1 {
  font-size: 3rem;
}

header .header-container .team-logo {
  background-color: #0000;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container .team-logo {
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    display: flex;
  }
}

@media (width <= 26.25rem) {
  header .header-container .team-logo {
    width: 8rem;
  }
}

header .header-container .team-logo img {
  width: 100%;
}

header .header-color {
  z-index: -3;
  background-color: #f3f3f3;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

header .secondary-color {
  z-index: -2;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
}

@media (width <= 48rem) {
  header .secondary-color {
    width: 100%;
    height: 1rem;
    position: static;
    transform: none;
  }
}

header .primary-color {
  z-index: -1;
  width: 40%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 60%;
}

@media (width <= 48rem) {
  header .primary-color {
    width: 100%;
    height: 1rem;
    position: static;
    transform: none;
  }
}

.nav-links {
  width: 100%;
  padding: 2rem 0;
}

.nav-links-container {
  justify-content: space-between;
  align-items: center;
  width: min(90%, 48rem);
  margin-inline: auto;
  display: flex;
}

@media (width <= 26.25rem) {
  .nav-links-container {
    flex-direction: column;
  }
}

.nav-links-container button {
  text-transform: uppercase;
  text-align: center;
  background-color: #fff;
  border: 1px solid #00f;
  border-radius: 0;
  width: 50%;
  padding: .8rem .5rem;
  font-size: .8rem;
  transition: background-color .2s, color .1s;
}

.nav-links-container button:hover, .nav-links-container button:focus {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
}

@media (width <= 26.25rem) {
  .nav-links-container button {
    width: 100%;
  }
}

.team-info {
  background-color: #f3f3f3;
  width: 100%;
  padding: 2rem 0;
}

.team-info-container {
  width: min(90%, 90rem);
  margin-inline: auto;
}

.cards-section {
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 2rem 0;
  display: flex;
}

.cards-section h2 {
  text-transform: uppercase;
  text-align: center;
  width: 100%;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

@media (width <= 26.25rem) {
  .cards-section h2 {
    margin-bottom: 1rem;
  }
}

.cards-section .stadium {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  margin-bottom: 1.5rem;
  display: flex;
}

@media (width <= 48rem) {
  .cards-section .stadium {
    width: 90%;
  }
}

.cards-section .stadium img {
  width: 100%;
}

.cards-section .cards-container {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem 1.5rem;
  width: 100%;
  display: grid;
}

@media (width <= 48rem) {
  .cards-section .cards-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 26.25rem) {
  .cards-section .cards-container {
    grid-template-columns: 1fr;
  }
}

.cards-section .cards-container .card h3 {
  text-transform: uppercase;
  margin-bottom: .5rem;
  font-size: 1.17rem;
}

.cards-section .cards-container .card p {
  font-size: 1rem;
}

.team-roster {
  background-color: #f3f3f3;
  width: 100%;
  padding: 2rem 0;
}

.team-roster-container {
  background-color: #fff;
  border: 1px solid #b9c1ba;
  grid-template-columns: 30% 70%;
  width: min(90%, 90rem);
  margin-inline: auto;
  display: grid;
}

@media (width <= 64rem) {
  .team-roster-container {
    grid-template-columns: 19.2rem 1fr;
  }
}

@media (width <= 26.25rem) {
  .team-roster-container {
    grid-template-columns: 100%;
  }
}

.photo-name-container {
  border-right: 1px solid #b9c1ba;
  overflow-x: auto;
}

.photo-name-container table {
  border-spacing: 0;
  text-align: left;
  width: 100%;
}

.photo-name-container th, .photo-name-container td {
  border-bottom: 1px solid #b9c1ba;
  padding: .5rem;
}

.photo-name-container th, .photo-name-container tr, .photo-name-container td {
  width: 100%;
}

.photo-name-container .heading {
  height: 4rem;
  font-size: 1.3rem;
}

.photo-name-container .row {
  height: 8rem;
  font-size: 1rem;
}

.photo-name-container a {
  justify-content: flex-start;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.photo-name-container img {
  margin-right: 1.5rem;
}

.other-player-info-container {
  overflow-x: auto;
}

@media (width <= 26.25rem) {
  .other-player-info-container {
    display: none;
  }
}

.other-player-info-container table {
  border-spacing: 0;
  text-align: left;
  width: max(100%, 51.2rem);
}

.other-player-info-container th, .other-player-info-container td {
  border-bottom: 1px solid #b9c1ba;
  align-items: center;
  padding: .5rem;
  display: flex;
}

.other-player-info-container tr {
  grid-template-columns: repeat(7, 14.2857%);
  display: grid;
}

.other-player-info-container tbody:nth-child(2n) {
  background-color: #f3f3f3;
}

.other-player-info-container .heading {
  height: 4rem;
  font-size: 1.3rem;
}

.other-player-info-container .row {
  height: 8rem;
  font-size: 1rem;
}

.error-container {
  width: min(90%, 90rem);
  margin: 5rem auto;
}

.error-container h3 {
  margin-bottom: .5rem;
  font-size: 1.6rem;
  font-weight: 700;
}

@media (width <= 26.25rem) {
  .error-container h3 {
    font-size: 1.28rem;
  }
}

.error-container p {
  font-size: 1rem;
}

@media (width <= 26.25rem) {
  .error-container p {
    font-size: .9rem;
  }
}

.hidden {
  display: none;
}

